.modal_bg_first {
    width: 100vw;
    height: 100vh;
    backdrop-filter: blur(15px);
    position: fixed;
    bottom: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal_bg_second {
    width: 100vw;
    height: 100vh;
    position: fixed;
    bottom: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal_container {
    width: 30%;
    height: 80%;
    border-radius: 12px;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    display: flex;
    flex-direction: column;
    padding: 25px;
}

.modal_container_det {
    width: 85%;
    height: 85%;
    border-radius: 12px;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.modal_container_det p {
    width: 100%;
    font-size: 1rem;
    margin-top: 1rem;
    text-align: center;
    word-wrap: break-word;
}

.modal_container .title {
    text-align: center;
}

.modal_container_det .title {
    text-align: center;
}

.modal_container_det textarea:read-only {
    background-color: white;
    text-align: center;
    height: 20%;
    width: 80%;
    font-size: 1rem;
    margin-top: 1rem;
    resize: none;
    border: none;
}

.modal_container_det h2 {
    text-align: center;
}

.modal_container_det ul {
    max-height: 150px;
    overflow: auto;
    align-items: center;
}

.users::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

.description::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

.description {
    overflow: scroll;
    max-height: 50px;
}

.modal_container_det .users {
    margin-left: 10%;
    max-width: 80%;
    overflow: scroll;
    height: 50px;
}

.modal_container .users {
    margin-left: 10%;
    max-width: 80%;
    overflow: scroll;
    height: 70px;
}

.modal_container .body {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.7rem;
    text-align: center;
}

.select__value-container{
    height: 100px;
    width: 300px;
}

.modal_container input {
    width: 100%;
    font-size: 1.7rem;
    margin-top: 1rem;
    text-align: center;
}

.modal_container .footer {
    flex: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 8rem;
}

.modal_container .footer_edit {
    flex: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 1rem;
}

.modal_container_det .footer {
    flex: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 1rem;
}

.footer button {
    width: 7rem;
    height: 3rem;
    margin: 10px;
    border: none;
    color: white;
    border-radius: 2rem;
    font-size: 1.5rem;
    cursor: pointer;
}

.footer_edit button {
    width: 7rem;
    height: 3rem;
    margin: 10px;
    border: none;
    color: white;
    border-radius: 2rem;
    font-size: 1.5rem;
    cursor: pointer;
}

form {
    width: 100%;
    height: 100%;
    font-size: 1.2rem;
    margin-top: 1rem;
}

.modal_container textarea {
    width: 100%;
    height: 30%;
    font-size: 1.2rem;
    margin-top: 1rem;
    box-sizing: border-box;
}

#cancel_button {
    background-color: rgb(255, 0, 0);
}

#cancel_button:hover {
    background-color: rgb(155, 0, 0);
}

#back_button {
    background-color: rgb(0, 0, 255);
}

#back_button:hover {
    background-color: rgb(0, 0, 155);
}

#submit_button, #submit_button_second{
    background-color: rgb(0, 255, 0);
}

#submit_button:hover, #submit_button_second:hover{
    background-color: rgb(0, 155, 0);
}

button:hover {
    transform: scale(1.1);
	transition-duration: 0.1s;
}

.is_owner {
    color: green;
    font-weight: bold;
    word-wrap: break-word;
}

.comment {
    word-wrap: break-word;
}

.name_description {
    word-wrap: break-word;
}

@media screen and (max-width: 600px) {
    .modal_container, .modal_container_det {
        width: 80%;
        height: 80%;
        overflow: auto;
    }

    .modal_container_det .users {
        margin-left: 30px;
    }
}